$main-color:#1A512E;
$second-color:#970009;


body{
  background-color: #fff;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 25px;
  color: #000;
  position: relative;
  overflow-x: hidden;
  padding-bottom: 200px;
}

.hero{
  width: 100%;
  padding-top: 70px !important;
  width: 100%;
  position: relative;
  background: $main-color url("../img/circle-scatter-haikei.svg") no-repeat left 0 top 0;
  background-size: 100% 100%;
  margin-bottom: 0;
}


.hero_grid{
  margin-top: 150px;
  display: flex;
  flex-direction: row;
  position: relative;
}

.hero_text{
  text-align: center;
  color: #fff;
  position: relative;
}

.hero_title{
  font-size: 90px;
  font-weight: 400;
}

.hero_image{
  position: absolute;
  z-index: 2; 
  right: 0;
  min-height: 500px;
  width: 50%;
  margin-top: 200px;
  img{
    width: 100%;
  }
}

.hero_wave{
  position: absolute;
  bottom: -200px;
  z-index: 1;
}

.btn-product{
  position: absolute;
  transform: translateY(100%) translateX(-50%);
  left: 50%;
  bottom: -100px;
  z-index: 2;
  font-size: 20px;
  padding: 10px 45px;
}


.product_photos{
  margin-top: 400px;
  display: flex;
  flex-wrap: wrap;
}

.product_photo_item{
  display: block;
  //background-color: #ccc;
  //background-clip: content-box;
  max-width: 16.66666667%;
  height: 200px;
  padding: 10px;

  img{
    width: 100%;
  }
}
